<template>
    <view class="box">
        <image :src="imgUrl" class="img" @longtap="downlond"></image>
        <view class="button" v-if="flag === true">
            <button type="warn" class="btn" @click="seva">保存图片到本地</button>
            <button type="default" class="bton" @click="clear">取消</button>
        </view>
    </view>
</template>

<script>
    export default {
        name: "",
        components: {

        },
        props: {},
        data() {
            return {
                imgUrl: '',
                flag: false
            }
        },
        methods: {
            downlond() {
                this.flag = true
            },
            clear() {
                this.flag = false
            },
            seva() {
                uni.downloadFile({
                    url: this.imgUrl,
                    success: res => {
                        if (res.statusCode === 200) {
                            uni.showToast({
                                title: '下载成功！',
                                duration: 2000
                            });
                        }
                    }
                });
            }
        },
        mounted() {

        },
        onLoad(options) {
            this.imgUrl = options.url
        },
        filters: {

        },
        computed: {

        },
        watch: {

        },
        directives: {

        }
    }
</script>

<style scoped lang="scss">
    .box {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(0, 0, 0, .9);
    }

    .img {
        width: 710rpx;
        height: 1000rpx;
        position: absolute;
    }
    .button {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
    }
    .btn{
        width: 750rpx;
        margin-bottom: 10rpx;
    }
    .bt0n {
        width: 750rpx;
    }
</style>
